// Normalize Styles
@import 'node_modules/normalize.css/normalize';

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.home-container {
  font-family: 'Montserrat', 'Lato', 'Avenir Next', 'Avenir', sans-serif;
  font-size: 62.5%;
}

.item {
  min-height: 400px;
  padding: 40% 30px 30px;
  position: relative;
  text-align: center;
  & > div {
    align-items: center;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    .content {
      align-items: center;
      background: rgba(white, 0.6);
      bottom: 20px;
      color: #15324A;
      display: flex;
      font-size: 1.6rem;
      height: auto;
      justify-content: center;
      left: 20px;
      line-height: 2.4rem;
      opacity: 0;
      letter-spacing: 0.2em;
      padding: 20px;
      position: absolute;
      right: 20px;
      text-transform: uppercase;
      top: 20px;
      transition: opacity 200ms ease-in-out;
      width: auto;
      @media screen and (max-width: 990px) {
        opacity: 1;
        font-size: 1rem;
        padding: 8px;
        top: initial;
      }
    }
    &:hover .content {
      opacity: 1;
    }
  }
}

.calendar-widget {
  align-items: flex-start;
  border: 1px solid #15324A;
  padding: 3rem 1.5rem 1.5rem;
  @media screen and (max-width: 990px) {
    display: block;
    height: auto;
    position: static !important;
  }
  &:after {
    background: rgba(white, 0.9);
    content: 'UPCOMING EVENTS';
    left: 0;
    font-size: 0.8rem;
    letter-spacing: 0.2em;
    line-height: 3rem;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    @media screen and (max-width: 990px) {
      background: transparent;
      top: 10px;
    }
  }
  ol {
    height: 100%;
    list-style: none;
    margin: 0;
    overflow: auto;
    padding: 0;
    width: 100%;
    li {
      font-family: 'Lato', 'Avenir Next', sans-serif;
      font-size: 0.8rem;
      padding: 1.5em 0;
      position: relative;
      & > div {
        line-height: 1rem;
        &.date,
        &.meta {
          color: darken(#A7BED1, 40%);
          letter-spacing: 0.05em;
          // opacity: 0.8;
        }
        &.title {
          color: darken(#A7BED1, 80%);
          font-size: 1rem;
          font-weight: 600;
          letter-spacing: 0.15em;
          line-height: 1.6rem;
        }
      }
      &:after {
        background: #15324A;
        content: '';
        height: 1px;
        left: 0;
        margin: auto;
        position: absolute;
        right: 0;
        bottom: 100%;
        width: 60px;
      }
    }
    @media screen and (max-width: 990px) {
      height: auto;
    }
  }
}

.item-block {
  display: flex;
  @media screen and (max-width: 990px) {
    flex-direction: column;
  }
  .w-100 {
    flex-grow: 1;
  }
  .w-70 {
    flex-grow: 2;
  }
  .w-30 {
    flex-grow: 1;
  }
  .item-block {
    flex-direction: column;
  }
  &.grid {
    padding: 10px;
    .item {
      padding: 10px;
      & > div {
        bottom: 10px;
        height: auto;
        left: 10px;
        right: 10px;
        top: 10px;
        width: auto;
      }
    }
  }
  &.full {
    min-height: 800px;
  }
}

.parallax-section {
  background-color: white;
  height: 600px;
  overflow: hidden;
  position: relative;
  width: 100%;
  @media screen and (max-width: 1280px) {
    height: 800px;
  }
  .parallax-content {
    align-items: center;
    color: #15324A;
    display: flex;
    flex-grow: 2;
    font-size: 2rem;
    letter-spacing: 0.1em;
    justify-content: center;
    height: 100%;
    padding: 20px;
    position: relative;
    width: 100%;
    z-index: 2;
    @media screen and (max-width: 1280px) {
      top: initial !important;
    }
  }
  .parallax-image {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 800px;
    left: 0;
    opacity: 0.8;
    position: absolute;
    top: 0;
    transform: translateZ(0);
    width: 100%;
    z-index: 1;
    @media screen and (max-width: 1280px) {
      height: 800px;
      top: 0 !important;
    }
  }
}


.parallax-section.featured-item {
  .parallax-content {
    color: #15324A;
    text-align: center;
    h1 {
      font-size: 2rem;
      font-weight: 500;
      letter-spacing: 0.1em;
      margin: 0;
      position: relative;
      text-transform: uppercase;
      z-index: 2;
      p {
        font-size: 0.8rem;
        font-weight: 300;
        opacity: 1;
        letter-spacing: 0.2em;
        text-transform: uppercase;
      }
    }
  }
  &:after {
    background-color: rgba(white, 0.4);
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }
}